import { API_URL } from "./config";
import { getJson } from "./helpers";

export const state = {
  recipe: {}
};
export const loadRecipe = async function(id) {
  try {
    const realRecipe = await getJson(`${API_URL}${id}`); 
      const recipe = {
      id: realRecipe.recipe.recipe_id,
      title: realRecipe.recipe.title,
      publisher: realRecipe.recipe.publisher,
      sourceUrl: realRecipe.recipe.source_url,
      ingredients: realRecipe.recipe.ingredients,
      image: realRecipe.recipe.image_url
    };
    state.recipe = recipe;
  } catch(err) {
      console.log('Fetching problem', err.message);
  }  
};