import icons from 'url:../../img/icons.svg';
import {controlRecipes} from '../controller';

class RecipeView {
  #parentElement = document.querySelector('.recipe');
  #data;
  #icons = icons;
  render(data) {
    this.#data = data;
    const markup = this.#generateMarkup();
    this.#clear();
    this.#parentElement.insertAdjacentHTML('afterbegin', markup);
  }
  #clear() {
    this.#parentElement.innerHTML = "";
  }
  renderSpinner = function(parentElem) {
    const markup = `
      <div class="spinner">
        <svg>
          <use href="${this.#icons}#icon-loader"></use>
        </svg>
      </div>
    `;
    parentElem.innerHTML = "";
    parentElem.insertAdjacentHTML('afterbegin', markup);
  }
  #generateMarkup() {
    return `            
      <figure class="recipe__fig">
        <img src="${this.#data.image}" alt="${this.#data.title} class="recipe__img" />
        <h1 class="recipe__title">
          <span>${this.#data.title}</span>
        </h1>
      </figure>
      <div class="recipe__details">
        <div class="recipe__info">
          <svg class="recipe__info-icon">
            <use href="${this.#icons}#icon-clock"></use>
          </svg>
        </div>
        <div class="recipe__info">
          <svg class="recipe__info-icon">
            <use href="${this.#icons}#icon-users"></use>
          </svg>
          <span class="recipe__info-data recipe__info-data--people"></span>
          <span class="recipe__info-text">servings</span>
          <div class="recipe__info-buttons">
            <button class="btn--tiny btn--decrease-servings">
              <svg>
                <use href="${this.#icons}#icon-minus-circle"></use>
              </svg>
            </button>
            <button class="btn--tiny btn--increase-servings">
              <svg>
                <use href="${this.#icons}#icon-plus-circle"></use>
              </svg>
            </button>
          </div>
        </div>
        <div class="recipe__user-generated">
          <svg>
            <use href="${this.#icons}#icon-user"></use>
          </svg>
        </div>
        <button class="btn--round">
          <svg class="">
            <use href="${this.#icons}#icon-bookmark-fill"></use>
          </svg>
        </button>
      </div>
      <div class="recipe__ingredients">
        <h2 class="heading--2">Recipe ingredients</h2>
        <ul class="recipe__ingredient-list">
          ${this.#data.ingredients.map(ingr => this.#generateMarkupIngredient(ingr)).join("")}          
        </ul>
      </div>
      <div class="recipe__directions">
        <h2 class="heading--2">How to cook it</h2>
        <p class="recipe__directions-text">
          This recipe was carefully designed and tested by
          <span class="recipe__publisher">${this.#data.publisher}</span>. Please check out
          directions at their website.
        </p>
        <a
          class="btn--small recipe__btn"
          href="${this.#data.sourceUrl}"
          target="_blank"
        >
          <span>Directions</span>
          <svg class="search__icon">
            <use href="${this.#icons}#icon-arrow-right"></use>
          </svg>
        </a>
      </div>   
    `;
  }
  #generateMarkupIngredient = function(ingr) {
    return `
      <li class="recipe__ingredient">
        <svg class="recipe__icon">
        <use href="${this.#icons}#icon-check"></use>
        </svg>                
        <div class="recipe__description">                  
          ${ingr}
        </div>
      </li>
    `;
  }
}
export default new RecipeView();
const events = ['hashchange', 'load'];
events.forEach(event => window.addEventListener(event, controlRecipes));